/*------------------------------------*\
    RESET
\*------------------------------------*/
/* http://meyerweb.com/eric/tools/css/reset/ 
    v2.0b1 | 201101 
    NOTE:WORK IN PROGRESS
    USE WITH CAUTION AND TEST WITH ABANDON */

html,body,div,span,applet,object,iframe,
h1,h2,h3,h4,h5,h6,p,blockquote,pre,
a,abbr,acronym,address,big,cite,code,
del,dfn,em,img,ins,kbd,q,s,samp,
small,strike,strong,sub,sup,tt,var,
b,u,i,center,
dl,dt,dd,ol,ul,li,
fieldset,form,label,legend,
table,caption,tbody,tfoot,thead,tr,th,td,
article,aside,canvas,details,figcaption,figure,
footer,header,hgroup,menu,nav,section,summary,
time,mark,audio,video{
    margin:0;
    padding:0;
    border:0;
    outline:0;
    font-size:100%;
    font:inherit;
    vertical-align:baseline;
}
/* HTML5 display-role reset for older browsers */
article,aside,details,figcaption,figure,
footer,header,hgroup,menu,nav,section{
    display:block;
}
body{
    line-height:1;
}
ol,ul{
    list-style:none;
}
blockquote,q{
    quotes:none;
}
blockquote:before,blockquote:after,
q:before,q:after{
    content:’’;
    content:none;
}
/* remember to define visible focus styles! 
:focus{
    outline:?????;
} */

/* remember to highlight inserts somehow! */
ins{
    text-decoration:none;
}
del{
    text-decoration:line-through;
}

table{
    border-collapse:collapse;
    border-spacing:0;
}

a{
	text-decoration:none;
}

a[href]{
	color:#111c24;
}


// FONT SMOOTHING
body{
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;	
}

h1{
	font-family:"Raleway",sans-serif;
	font-weight:700;
	font-size:34px;
	@include bp(mamabear){
		font-size:40px;
	}
	@include bp(papabear){
		font-size:50px;
	}
}

h1:after{
	content:"";
	display:inline-block;
	width:7px;
	height:7px;
	border-radius:7px;
	background-color:#fff;
	margin-left:5px;
}


h2{
	font-family: 'Lato', sans-serif;
	font-weight:400;
	font-size:19px;
	line-height:1.6;
	@include bp(babybear){
		font-size:22px;
	}
	@include bp(papabear){
		font-size:28px;
	}
}

p{
	font-family: 'Merriweather', serif;
	color: rgba(18, 28, 37, 0.9);
	font-size:13px;
	line-height:2;
	@include bp(babybear){
		font-size:14px;
	}
	@include bp(babybear){
		font-size:16px;
	}
}






.button-expand-container.more{
	margin-top:25px;
	display:inline-block;
	@include bp(papabear){
		margin-top:45px;
	}
}


// button icon expand animation

.button-expand-container{
	position:relative;
}


.button-more{
		font-family:"Lato",sans-serif;
		line-height:20px;
		font-weight:700;
		font-size:13px;
		padding-left:40px;
		text-transform: uppercase;
		display: inline-block;
		letter-spacing:1px;
	@include bp(babybear){
		font-size:15px;
	}
	@include bp(papabear){
		font-size:16px;
		line-height:22px;
		padding-left:50px;
	}
}

.more-icon-container{
	width:20px;
	height:20px;
	float: left;
    margin-right: 15px;
    margin-left:6px;
    position:absolute;
    top:0;
    z-index:-1;
    &:after{
	    content: "";
	    position: absolute;
	    width: 33px;
	    height: 33px;
	    border-radius: 33px;
	    left: 0;
	    top: 0;
	    border: 1px solid #111c24;
	    margin-top: -7px;
	    margin-left: -7px;
	    transition: opacity 0.5s ease-out, transform 0.3s ease-in;
    }
}



.button-more:hover + .more-icon-container{
		#icon-plus-line{
			stroke:#e81d44;
		}
		&:after{
// 		border-color:#e81d44;
		opacity:0;
		transform:scale3d(5, 5, 1);
		transition:opacity 0.3s ease-out,transform 0.3s ease-out; //animation hoveron
		}
}

.icon-home{
	font-family: 'Raleway', sans-serif;
    font-weight: 700;
    font-size: 45px;
    position:absolute;
    top:15px;
    left:20px;
}

.icon-home:hover{
	color:#e81d44;
	cursor:pointer;
}

// button icon expand animation end

