.process{
	overflow:hidden;
	padding:100px 0;
	background-color:#111c24;
	h1, .title, .quote, p{
		color:#fff;
	}
	h1{
		padding-bottom:30px;
	}
	.ring{
		color:rgba(255,255,255,0.9);
		line-height:35px;
		font-size:20px;
		height:40px;
		width:40px;
		border-radius:40px;
		border:1px solid rgba(255,255,255,0.4);
		text-align: center;
		margin-right:12px;
		margin-left:-10px;
	}
	
	ul{
		opacity:1;
		-webkit-transition:opacity 0.3s ease-out 0.1s;
		transition:opacity 0.3s ease-out 0.1s;
	}
	
	li div{
		display:inline-block;
	}
	li{
		font-family:"Raleway", sans-serif;
		font-weight:600;
		padding:7px 0;
	}
	.process-container{
		position:relative;
	}
	
	.slide-info div{
		display:inline-block;
	}
	
	.slide-info .back-to-process{
		color:rgba(255,255,255,0.9);
		height:45px;
		width:45px;
		border-radius:45px;
		border:1px solid rgba(255,255,255,0.4);
		text-align: center;
		margin-right:12px;
		margin-left:-10px;
	}
	
	.slide-info{
		position: absolute;
		top:0;
		left:0;
		z-index:2;
		-ms-transform: translate3d(300%,0,0); /* IE 9 */
		-webkit-transform: translate3d(300%,0,0); /* Chrome, Safari, Opera */
		transform: translate3d(300%, 0, 0);
		
		-webkit-transition: -webkit-transform 0.3s ease-out;
		-moz-transition: -moz-transform 0.3s ease-out;
       -o-transition: -o-transform 0.3s ease-out;
        transition: transform 0.3s ease-out;
	}	
	
}


// interactions
.process{	
	
// 	hover
	li:hover{
		cursor:pointer;
		.title{
			color:#e81d44;
		}
		.ring{
			border-color:#e81c45;
			color:#e81d44;
		}
	}
	
	
// 	bring in detail
	.process-container.bringDetail ul{
		opacity:0;
		-webkit-transition:opacity 0.3s ease-out;
		transition:opacity 0.3s ease-out;
	}
	
	.process-container.bringDetail .slide-info{
		-ms-transform: translate3d(0,0,0); /* IE 9 */
		-webkit-transform:translate3d(0,0,0); /* Chrome, Safari, Opera */
		transform: translate3d(0,0,0);
		
		-webkit-transition: -webkit-transform 0.3s ease-out 0.2s;
		-moz-transition: -moz-transform 0.3s ease-out 0.2s;
       -o-transition: -o-transform 0.3s ease-out 0.2s;
        transition: transform 0.3s ease-out 0.2s;
        

	}
	
}

//OVERLAYS

.overlay-action{


	overflow-y: scroll;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s 0.5s, opacity 0.5s linear;


  position: fixed;
	top:0;
	bottom:0;
	right:0;
	left:0;
}

.overlay-menu{
	background-color:rgba(255,255,255,0.97);
	z-index:3;
}

.overlay-about{
	background-color:rgba(255,255,255,0.97);
	z-index:2;
	
}

.overlay-action.open{
  visibility:visible;
  opacity: 1;
  transition: opacity 0.5s linear;
}



.close-overlay{
	position: absolute;
	right:15px;
	top:15px;
	z-index:3;
	@include bp(papabear){
		right:30px;
		top:20px;
	}
}


.close-overlay:hover{
	cursor:pointer;
	#icon-close-line{
		stroke:#e81d44;
	}
}


