.main-menu-container{
	position:absolute;
	width:80%;
	margin:0;
	z-index:3;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -52%);
	-webkit-transform:translate(-50%, -52%);
	-moz-transform:translate(-50%, -52%);
	-o-transform:translate(-50%, -52%);	
}

.work-sub{
	position:absolute;
	width:100%;
	padding-top:20px;
	 visibility: hidden;
	 opacity: 0;
	 transition: visibility 0s 2s, opacity 2s linear; //off	
}

.close-work-icon-container,.outer-ring{
		opacity:0;
	}


.close-work-icon-container{
	position:absolute;
	width:26px;
	left:50%;
	margin-left:-13px;
	top:-30px;
	svg{
		width:27px;
	}
}

.outer-ring{
	content:"";
	position:absolute;
	left:50%;
	margin-left:-20px;
	z-index:-1;
	width:40px;
	height:40px;
	border-radius:40px;
	border:1px solid #111c24;
    top: -38px;
    transition:opacity 0.5s ease-out,transform 0.3s ease-in; //animation hoveroff
}


.close-work-icon-container:hover + .outer-ring{
	opacity:0!important;
	transform:scale3d(5, 5, 1);
	transition:opacity 0.3s ease-out,transform 0.3s ease-out; //animation hoveron
}

.close-work-icon-container:hover{
	cursor:pointer;
	#icon-close-line{
		stroke:#e81d44;
	}
}







.main-menu-container.work-active{
	
	.no-drop{
		opacity:0;
		transition: opacity 0.4s ease-out;
	}

	.work-item{
		
			transform: translate3d(0, -500%,0);
			-webkit-transform:translate3d(0, -500%,0);
			-moz-transform:translate3d(0, -500%,0);
			-o-transform:translate3d(0, -500%,0);	
			
			-webkit-transition: -webkit-transform 0.3s ease-out 0.6s; /* Changed here */ 
			-moz-transition: -moz-transform 0.3s ease-out 0.6s;
			-o-transition: -o-transform 0.3s ease-out 0.6s;
			transition: transform 0.3s ease-out 0.6s;
			

	}
	.work-sub{
		visibility: visible;
		opacity: 1;
		transition: opacity 0.8s ease-out 1.3s; //turned on
	}
	

	.close-work-icon-container,.outer-ring{
		opacity:1;
	}


}







